.home-content {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  z-index: 999;
  background-color: transparent;
  flex-direction: column;

  h1 {
    max-width: 115.2rem;
    word-break: keep-all;
    overflow: hidden;
    font-size: 7rem;
    font-weight: 500;
    display: none;
    line-height: 8rem;

    em {
      color: #0090b2;
      font-style: normal;
    }
    &.fadeIn {
      display: block;
    }
  }
  p {
    margin: 2rem auto;
    font-size: 1.4rem;
    font-weight: 700;
    span {
      margin: 0 1rem;
      opacity: 0.9;
      color: #fff;
    }
  }
  .mission-btn {
    font-family: 'Montserrat', sans-serif;
    font-size: 1.5rem;
    border: 0.2rem solid #fff;
    border-radius: 0.3rem;
    font-weight: 700;
    padding: 1rem 1.5rem;
    cursor: pointer;
    transition: background-color 0.3s ease, border 0.3s ease, color 0.3s ease,
      opacity 0.3s ease-in-out, outline 0.3s ease;
    &:hover {
      background-color: #fff;
      color: #222;
    }
  }
}

@include tab {
  .home-content {
    h1 {
      max-width: 92.1rem;
      font-size: 6rem;
    }
  }
}

@include mob-large {
  .home-content {
    margin: 0 auto;
    h1 {
      font-size: 3rem;
      max-width: 57.6rem;
      line-height: 4rem;
    }
  }
}

@include mob-mid {
  .home-content {
    h1,
    p {
      max-width: 38.2rem;
    }
    p {
      line-height: 2rem;
    }
  }
}

@include mob-small {
  .home-content {
    h1,
    p {
      max-width: 33.7rem;
    }
  }
}
